nav.main{
    padding: 0, 30px, 30px, 0;
}

.main ul{
    list-style: none;

}
nav.main a{
    display: block;
    text-decoration: none;
    color: #000;
    text-transform: uppercase;
    font-weight: bold;
    padding: 10px;
    transform: .2s;
}

nav.main a:hover{
    transform: translateX(20px);
}

nav.main a.active{
    border-left: 10px gray solid;
    transform: translateX(0);
}

nav.main li:nth-child(1) a.active{
    border-left-color: rgb(62, 197, 62);
}
nav.main li:nth-child(2) a.active{
    border-left-color: rgb(48, 76, 231);
}
nav.main li:nth-child(3) a.active{
    border-left-color: rgb(228, 169, 43);
}
nav.main li:nth-child(4) a.active{
    border-left-color: rgb(202, 50, 88);
}